.ninja-profiles {
  min-width: 21em;
  display: flow-root;
}

@media (max-width: 375px) {
  .ninja-profiles > header {
    font-size: 0.6em;
  }
}

.ninja-profiles > header {
  min-width: 15em;
  position: sticky;
  top: 0px;
  padding: 1em;
  background-color: #002B55;
  color: #80C565;
  border-bottom: 6px solid #80C565;
}

.ninja-profiles h1 {
  text-align: center;
  font-size: 2em;
}