.ninja-card {
  min-width: 15em;
  display: inline-block;
  width: 15em;
  border: 1px solid #dcdde0;
  padding: 0.5em;
  margin: 0.5em;
  box-shadow: 1px 2px 3px rgba(220,221,224,0.6)
}

@media (max-width: 375px) {
  .ninja-card {
    min-width: 8em;
    width: 8em;
  }
}

.ninja-card .portrait {
  width: 100%;
  object-fit: scale-down;
}

.ninja-card .fab {
  color: black;
}

.ninja-card address .fab:hover {
  position: relative;
  top: -3px;
  color: #80C565;
}

.ninja-card .details {
  display: flex;
  margin: 0.5em 0em;
}

.ninja-card .details h2 {
  padding-right: 1em;
  line-height: 1.4;
}

.ninja-card .details .office {
  font-size: smaller;
} 

.ninja-card .details address {
  flex: 1 0 fit-content;
  text-align: right;
  letter-spacing: 0.2em;
}

@media (max-width: 375px) {
  .ninja-card .details address{
    display: flex;
    flex-direction: column;
  }
}
