.toolbar input[type="checkbox"] {
  display: none;
}

.toolbar input + label {
  padding: 0.3em;
  margin: 0.3em;
  border: 1px solid #80C565;
  border-radius: 0.2em;
  text-transform: capitalize;
}
.toolbar input:checked + label {
  background: white;
  color: black;
  /*font-weight: bold;*/
  border-color: white;
}

.toolbar form {
  display: flex;
  flex-wrap: wrap;
  padding-top: 1em;
  justify-content: center;
}